import request from '@/utils/request'

const baseUrl = '/api/admin/user'

const userApi = {}

userApi.login = (params) => {
  return request({
    url: `${baseUrl}/login`,
    params: params,
    method: 'get'
  })
}

userApi.getAllPermission = (params) => {
  return request({
    url: `${baseUrl}/permission`,
    params: params,
    method: 'get'
  })
}

userApi.info = () => {
  return request({
    url: `${baseUrl}/info`,
    method: 'get'
  })
}

userApi.findAll = () => {
  return request({
    url: `${baseUrl}`,
    params: params,
    method: 'get'
  })
}

userApi.page = (params) => {
  return request({
    url: `${baseUrl}/page-plus`,
    params: params,
    method: 'get'
  })
}

userApi.get = userId => {
  return request({
    url: `${baseUrl}/${userId}`,
    method: 'get'
  })
}

userApi.save = (params) => {
  return request({
    url: `${baseUrl}/save`,
    data: params,
    method: 'post'
  })
}

userApi.delete = userId => {
  return request({
    url: `${baseUrl}/${userId}`,
    method: 'delete'
  })
}

export default userApi
